<template>
  <div class="recognition-detail">
<!--    <van-nav-bar left-arrow @click-left="goBack" title="业主人脸录入"></van-nav-bar>-->
    <navigation-bar :pageName="pageName" @onLeftClick="goBack">
    </navigation-bar>
    <div class="detail-box">
      <van-swipe class="my-swipe" indicator-color="#00cccc" v-if="roomInfo.length>0">
        <van-swipe-item v-for="(item,index) in roomInfo" :key="index">
          <div class="content">
            <div class="item">
              <span class="title">姓名</span>
              <span class="value">{{info.name}}</span>
            </div>
            <div class="item">
              <span class="title">性别</span>
              <span class="value">{{info.sex=="0"?"男":"女"}}</span>
            </div>
            <div class="item">
              <span class="title">手机号</span>
              <span class="value">{{info.phone}}</span>
            </div>
            <div class="item">
              <span class="title">身份</span>
              <span class="value">{{info.groupName}}</span>
            </div>
            <div class="item">
              <span class="title">住址</span>
              <span class="value">{{item.doorNum}}</span>
            </div>
            <div class="item">
              <span class="title">是否录入人脸</span>
              <span class="value">{{hasPic?"是":"否"}}</span>
            </div>
          </div>
        </van-swipe-item>
      </van-swipe>
      <div class="content" v-else>
        <div class="item">
          <span class="title">姓名</span>
          <span class="value">{{info.name}}</span>
        </div>
        <div class="item">
          <span class="title">性别</span>
          <span class="value">{{info.sex=="1"?"男":"女"}}</span>
        </div>
        <div class="item">
          <span class="title">手机号</span>
          <span class="value">{{info.phone}}</span>
        </div>
        <div class="item">
          <span class="title">身份</span>
          <span class="value">{{info.groupName}}</span>
        </div>
        <div class="item">
          <span class="title">住址</span>
          <span class="value">暂无数据</span>
        </div>
        <div class="item">
          <span class="title">是否录入人脸</span>
          <span class="value">{{hasPic?"是":"否"}}</span>
        </div>
      </div>
      <div class="img-area">
        <img :src="hasPic?undefaultPic:defaultPic" />
        <button class="upload-btn" @click="fasongshijian">
          <img src="../../assets/imgs/recognition/camer.png" />
          {{hasPic?"重新上传":"录入人脸照片"}}
        </button>
      </div>
    </div>
    <!-- 上传成功弹窗 -->
    <!-- <van-dialog
      class-name="confirm-dialog"
      v-model="showConfirm"
      confirm-button-text="知道了"
      confirm-button-color="#fff"
    >
      <img src="../../assets/imgs/recognition/success.png" />
      <div>上传成功</div>
    </van-dialog> -->
    <!-- 相册、拍照 -->
    <van-action-sheet
      v-model="showPhotoSelectSheet"
      :actions="actions"
      cancel-text="取消"
      round
      close-on-click-action
      @select="onClickedGetPhoto"
    />
  </div>
</template>

<script>
import { store, storage } from "@/utils/store";
import { ActionSheet } from "vant";
import { getFaceInfoDetail, getPic } from "xt/services/recognition.js";
import { face2Device } from "xt/services/register";
import NavigationBar from "../../../title/NavigationBar";
export default {
  name: "recognitionDetail",
  components: {
    NavigationBar,
    [ActionSheet.name]: ActionSheet
  },
  data() {
    return {
      pageName:"业主人脸录入",
      custNews: null, // 一些登录的信息
      faceId: "",
      info: {},
      roomInfo: [],
      hasPic: false,
      defaultPic: require("../../assets/imgs/recognition/default.png"),
      undefaultPic: "",
      // showConfirm: false,
      showPhotoSelectSheet: false, //相册、拍照上传
      actions: [{ name: "拍照" }, { name: "从手机相册选择" }]
    };
  },
  created() {
    // this.custNews = store.get("custNews");

  },
  mounted() {
    // this.faceId = this.$route.query.id;
    // console.log("人脸详情" + this.faceId);

    console.log("url获取参数————————————————————————");
    console.log(window.location.search);

    console.log("getStorage——————————————-——————————");
    console.log(storage.getStorage("accessToken", false));

    console.log("fagongbo——————————————————————————-");
    this.fasongshijian();

    console.log("jieshu----------------");



    // this.getFaceInfoDetail();
  },
  methods: {
    fasongshijian(){
      console.log("fasongshijianle---------------------");
      var evObj = document.createEvent('xtevent');
      this.dispatchEvent(evObj);
    },
    goBack() {
      this.$router.push({ path: "/recognition" });
    },
    getFaceInfoDetail() {
      this.loading = true;
      let that = this;
      getFaceInfoDetail(this.faceId)
        .then(res => {
          that.loading = false;
          let detail = res.data;
          that.info = res.data.model;
          that.roomInfo = res.data.rooms;
          if (that.info.faceFileId) {
            // 有照片
            console.log("有照片,调用download展示照片");
            let picId = that.info.faceFileId;
            that.getPic(picId); // 有照片,调用download展示照片
          }
        })
        .catch(err => {
          that.loading = false;
          console.log("获取人脸详情报错了", err);
          that.$toast(err);
        });
    },
    getPic(id) {
      this.loading = true;
      console.log("--getPic--download--");
      getPic(id).then(res => {
        console.log("获取图片成功", res);
        let blob = new Blob([res.data], {
          type: "application/octet-stream;charset=utf-8"
        });
        console.log(URL.createObjectURL(blob));
        this.undefaultPic = URL.createObjectURL(blob);
        this.hasPic = true;
        this.loading = false;
      });
    },
    // 点击录入人脸照片
    onClickedGetPhoto(item, index) {
      this.getPictureData(index);
    },
    // 通过摄像头或者本地相册进行图片上传
    getPictureData(selectIndex) {
      selectIndex ? this.getLocalPicture() : this.useCamera();
    },
    // 通过相册获取
    getLocalPicture() {
      let that = this;
      this.$plugin.usePlugin("getPictures")(
        1,
        images => {
          images.forEach(imageData => {
            that.uploadPhotoFile(imageData);
          });
        },
        error => {
          that.$toast(error);
        }
      );
    },
    // 通过摄像头获取
    useCamera() {
      this.$plugin.usePlugin("camera")(
        imageData => {
          // if (/(Android)/i.test(navigator.userAgent)){
          //   this.getLocalPicture();
          // } else {
          this.uploadPhotoFile(imageData);
          // }
        },
        error => {
          this.$toast(error);
        },
        { allowsEditing: true }
      );
    },
    // 上传图片数据
    uploadPhotoFile(fileURL) {
      let loading = store.get("showLoadingView");
      if (loading) {
        loading(true);
      }
      console.log("--uploadPhotoFile--");
      let that = this;
      let reqUrl = storage.getStorage("xtBaseUrl") + "/web/face/check/pic";
      let options = new FileUploadOptions();
      let params = {};
      params.file = fileURL;
      params.type = "face";
      params.edit = "edit";
      params.faceId = this.faceId;
      options.params = params;
      let token = storage.getStorage("controlToken");
      options.headers = { contentType: "multipart/form-data" };
      options.headers = { Authorization: token };
      let success = res => {
        if (loading) {
          loading(false);
        }
        console.log(JSON.stringify(res));
        let resData = JSON.parse(res.response);
        console.log("--code--" + resData.code);
        if (resData.code == 200) {
          console.log("facePic--true");
          console.log(JSON.stringify(resData.data.fileupload));
          let picId = resData.data.fileupload.id; // 拿到上传的照片的id
          // that.showConfirm = true; // 上传成功弹窗
          that.getPic(picId); // 调用download展示照片
          that.face2Device(); // 下发设备
          that.$toast("上传成功，正在下发设备");
        } else {
          console.log("facePic--false");
          that.picUrl = "";
          that.$toast(resData.message);
        }
      };
      let fail = err => {
        if (loading) {
          loading(false);
        }
        console.log(JSON.stringify(err));
        that.picUrl = "";
        that.$toast("上传失败，请重新上传");
      };
      this.$plugin.usePlugin("fileTransfer")(
        reqUrl,
        encodeURI(fileURL),
        success,
        fail,
        options
      );
    },
    // 下发设备
    face2Device() {
      console.log("--face2Device--");
      this.loading = true;
      face2Device(this.faceId, this.custNews.AREA_CODE)
        .then(res => {
          this.loading = false;
          console.log(this.faceId);
          console.log(JSON.stringify(res));
          if (res.code == "200") {
            // 此时已成功上传人脸信息
            this.$toast("成功下发设备");
          } else {
            this.$toast("下发设备失败，请去物业后台进行人脸下发");
          }
        })
        .catch(err => {
          this.loading = false;
          this.$toast("下发设备失败，请去物业后台进行人脸下发");
        });
    }
  }
};
</script>

